
html ,body{
  scroll-behavior: smooth;
   
}

body{
  background: #eee;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000; 
  margin: 0;
  padding:0;
  
}
.header {
  display: block;
  /* margin: 0 auto; */
  position: fixed;
  transition: top 0.3s;
  width: 100%;
  top: 0;
  /* max-width: 100%; */
  /* box-shadow: none; */
  background-color: grey;
  opacity: 0.85;
  text-align: right;
  font-size: 100px;
  font-family: 'Bodoni Moda', serif;
  color: white;
  position: fixed;
  height: 200px;
  /* overflow: hidden; */
  z-index: 100;
}
/* #header{
  
  top: 0;
