/* login */
.center, .contents {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
/* .contents{
  display: none;
} */
#click{
  display: none;
}
.click-me {
  display: block;
  width: 160px;
  height: 50px;
  background: #27ae60;
  border: 1px solid #26a65b;
  color: white;
  text-align: center;
  font-size: 22px;
  line-height: 50px;
  border-radius: 3px;
  transition: .5s;
}
.contents{
  display: none;
  visibility: hidden;
  width: 450px;
  height: 350px;
  background: white;
  border-radius: 3px;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,.4);
}
#click :checked ~ .contents{
  visibility: visible;
}
.headers{
  height: 68px;
  background: #27ae60;
  overflow: hidden;
  border-radius: 3px 3px 0 0;
  box-shadow: 0 2px 3px rgba(0,0,0,.2);
}
.headers > h2{
  color: white;
  padding-left: 32px;
  font-weight: normal;
}

.swiper-container {
  width: 100vw;
  height: 45vh;
}

.swiper-wrapper {
  padding-inline-start: 0;
}
.swiper-pagination-bullet{
  height: 15px !important;
  width: 15px !important;
}



.navigation {
  position: fixed;
  top: 198px;
  left: -100%;
  height: 100vh;
  width: 100vw;
  background-color: grey;
  /* opacity: 0.7; */
  z-index: 5;
  transition: 0.3s;
  font-size: 90px;
  font-family: 'Bodoni Moda', serif;
}

.navigation > div > ul {
  list-style-type: none;
}

.navigation > div > ul > li > *{
  
  text-decoration: none;
  list-style: none;
  color: whitesmoke;
  
}

.btn a:hover, .btn a:active, .btn a.active {
  color: brown;
}

.navigation > div > ul{
  display: flex;
  flex-direction: column;
  margin: 0;
}

.navigation > div > ul > li{
  display: block;
  background-color: grey;
  text-align: center;
  padding: 3px 6px 3px 6px;
}

.app-wrapper{
  width: 100vw;
  height: 100vh;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

#hamburgerIcon {
  position: fixed;
  top: 1em;
  left: 1em;
  margin: 0;
  padding: 0;
  width: 60px;
  height: 60px;
  z-index: 10;
}

#boxClick {
  visibility: hidden;
}

#hamburgerIcon > div {
  background-color: whitesmoke;
  border-radius: 3px;
  content: '';
  display: block;
  height: 7px;
  width: 100%;
  transition: all .3s ease-in-out;
}

#hamburgerLine2, #hamburgerLine1 {
  margin-bottom: 12px;
}

#hamburgerLine1, #hamburgerLine2, #hamburgerLine3{
  transform: none;
}

.fa {
  position: absolute;
  /* flex-direction: row; */
  bottom: 150px;
  right: 20px;
  /* transform: translate(-50%, -50%); */
  margin:0;
  padding:0;

  /* align-items: center; */
  /* justify-content: center; */
  
}
.fa ul{
  display: flex;
  flex-direction: row;
}

.fa ul li {
  list-style: none;
}

.fa ul li a {
  position: relative;
  width: 90px;
  height: 90px;
  display: block;
  text-align: center;
  margin: 0 30px;
  border-radius: 50%;
  padding: 6px;
  box-sizing: border-box;
  text-decoration: none;
  box-shadow: 0 10px 15px rgba(0,0,0,1);
  background: linear-gradient(0deg, #ddd, #fff);
  transition: .5s;
  z-index: 20;
}

/* .fa ul li a:hover {
  box-shadow: 0 2px 5px rgba(0,0,0,0.3);
  text-decoration:none;
} */

.fa ul li a .fab {
  width: 100%;
  height:100%;
  display:block;
  background: linear-gradient(0deg, #fff, #ddd);
  border-radius: 50%;
  line-height: calc(80px - 8px);
  font-size:45px;
  color: #262626;
  transition: .5s;
}

.fa ul li:nth-child(1) a:hover .fab {
  color: #3b5998;
}


.fa ul li:nth-child(2) a:hover .fab {
  color: #007bb6;
}

.drop-menu > ul > li > *{
  padding: 3px 6px 3px 20px;
}

.drop-menu  {
  display: none;
  translate: 3s;
}


.main {
  padding: 3px 6px 3px 40px;
