.modal {
  display: none; 
  position: fixed;
  padding-top: 100px; 
  left: 0;
  top: 0;
  width: 100%; 
  height: 100%; 
  overflow: auto; 
  background-color: rgb(0,0,0); 
  background-color: rgba(0,0,0,0.4); 
  z-index: 1000;
}

.modal-content {
  position: relative;
  top: 100px;
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 50%;
  height: 30%;
}


.close {
  color: #aaaaaa;
  float: right;
  font-size: 40px;
  font-weight: bold;
} 

.rating {
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotateY(180deg);
  /* display: flex;
  flex-direction: row;
  justify-content:space-between;
  align-items: center; */
}
.rating > input {
  display: none;

}

 .rating > label {
  display: block;
  cursor: pointer;
  width: 50px;
  /* background-color: #ccc; */
}

.rating > label:before {
  content: '\f005';
  font-family: fontAwesome;
  position: absolute;
  display: block;
  font-size: 50px;
  color: #888;
} 
.rating > label:after {
  content: '\f005';
  font-family: fontAwesome;
  position: relative;
  display: block;
  font-size: 50px;
  color: brown;
  top: 0;
  opacity: 0;
  transition: .5s;
  text-shadow: 0 2px 5px rgba(0,0,0,.5);
} 
.rating > label:hover:after,
.rating > label:hover ~ label:after,
.rating > input:checked ~ label:after{
  opacity: 1;
}
